.burger {
  position: relative;
  cursor: pointer;
  padding-top: 2px;
  padding-bottom: 2px;
  transition: transform 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.burger .burger-line {
  display: block;
  width: 45px;
  height: 2px;
  background: #292B33;
  transition: transform 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95), margin 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  overflow: hidden;
  transform-origin: center;
}

.burger .line-bottom {
  margin-top: 11px;
}

.burger .line-bottom::after {
  content: '';
  display: block;
  width: 45px;
  height: 2px;
  transform: translateX(-100%);
  transition: transform 0.7s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  background: #EC2026;
  transition-delay: 0.3s;
}

.burger .line-top::after {
  content: '';
  display: block;
  width: 45px;
  height: 2px;
  transform: translateX(-100%);
  transition: transform 0.7s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  background: #EC2026;
}

.burger.clicked {
  transform: rotate(180deg);
}

.burger.clicked .line-top {
  transform: rotate(-45deg);
}

.burger.clicked .line-bottom {
  transform: rotate(45deg);
  margin-top: -2px;
}

.burger:hover .line-top::after, .burger:hover .line-bottom::after {
  transform: translateX(100%);
}

.burger:hover .line-bottom::after {
  transition-delay: 0.2s;
}

