  overflow: hidden;
  margin: 0;
  height: 34vw;
}

.slideshow::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.4s;
}

.slide {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  overflow-x: hidden;
  pointer-events: none;
  opacity: 0;
}

.slide--current {
  opacity: 1;
  z-index: 999;
  pointer-events: auto;
  position: relative;
}

.slide__wrap {
  width: 100%;
  overflow: hidden;
  height: 100%;
  position: absolute;
}

.slide__bg,
.slide__img {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: white;
  background-size: cover;
  position: absolute;
  pointer-events: none;
  background-position: 50% 50%;
}

.boxnav {
  display: flex;
  width: 64vw;
  justify-content: space-between;
  margin-top: 45px;
}

.boxnav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  left: auto;
  right: 0;
  flex: none;
  cursor: pointer;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
}

.boxnav__item:focus {
  outline: none;
}

.boxnav__item svg {
  display: block;
  width: 94px;
  height: 16px;
}

.boxnav__item--prev:hover,
.boxnav__item--next:hover {
  color: #858585;
}

.boxnav__label {
  padding: 0 0.5rem;
  font-size: 1.15rem;
  position: relative;
}

.boxnav__label--total::before {
  content: '';
  position: absolute;
  right: 100%;
  top: 0;
  width: 2px;
  height: 1.45rem;
  background: #000;
  transform: rotate(22.5deg);
}

.icon--caret-rot {
  transform: rotate(180deg);
}

.scroll-progress {
  position: fixed;
  bottom: 4vh;
  right: 5%;
  z-index: 100;
  width: 54px;
  height: 54px;
  transition: opacity .5s ease;
  pointer-events: none;
}

.scroll-progress .svg {
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(-94deg);
}

.scroll-progress .progress-percent {
  font-family: 'Ogg-Roman', 'Cormorant Garamond', serif;
  font-weight: 400;
  font-size: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.scroll-progress .arrow-top {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  cursor: pointer;
  z-index: 10;
}

.form {
  padding: 1px 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  color: white;
  position: relative;
}

.form .stroke-a {
  align-self: flex-end;
  position: absolute;
  bottom: 18px;
}

.input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.input-wrapper.first {
  margin-top: 0px;
}

.input-wrapper input {
  font-size: 16px;
  outline: none;
  border: 0;
  padding-top: 20px;
  padding-bottom: 15px;
  background: transparent;
  color: black;
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
}

.input-wrapper input:focus {
  border: none;
  color: black;
}

.input-wrapper label {
  position: absolute;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1em;
  bottom: 0px;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  border-bottom: 1px solid #292B33;
}

.input-wrapper label::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid;
  border-color: black;
  width: 0px;
  transition: width 0.5s ease, border-color .5s ease;
  align-self: center;
  justify-self: center;
}

.label-content {
  position: absolute;
  font-size: 18px;
  font-family: 'Ogg-Roman', 'Cormorant Garamond', serif;
  font-weight: 400;
  color: black;
  left: 0;
  bottom: 10px;
  opacity: 0.5;
  transform: translateY(-7px);
  transition: opacity 0.5s ease, transform .5s ease;
}

.focus + .label .label-content {
  opacity: 0;
  transform: translateY(-15px);
}

.focus + .label:after {
  width: 100%;
}

.form-button {
  border: none;
  background: transparent;
  color: black;
  cursor: pointer;
  outline: none;
  font-size: 17px;
  overflow: hidden;
  z-index: 2;
  width: 100%;
  height: 100%;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  line-height: 1em;
  padding: 0 0px;
  width: fit-content;
  position: relative;
  display: block;
  transition: color .4s ease;
}

.form-validate-text {
  color: #F44336;
  display: block;
  margin-top: 5px;
  font-size: 14px;
  position: absolute;
  transition: opacity .3s ease;
  opacity: 0;
  bottom: -27px;
}

